<template>
  <div class="detail">
    <!-- @click-left="onClickLeft"
    @click-right="onClickRight"-->
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      right-text="分享"
      left-arrow
      :fixed="true"
      @click-left="goback"
      @click-right="showShare = true"
    />
    <div class='imgs'>
      <img class="auto-img" :src="productDetail.large_img" alt />
    </div>

    <div class="detail-content">
      <div class="clearfix detail-info">
        <div class="fl pro-name">{{productDetail.name}}</div>
        <div class="fr like" @click="like">
          <div :class="[isLike?'iconfont iconshoucang-copy': 'iconfont  iconshoucang']"></div>
          <!-- <img
            class="auto-img"
            :src="isLike?'../assets/images/like_active.png':'../assets/images/like.png'"
            alt
          /> -->
        </div>
      </div>

      <!-- 商品规格 rules里面数组rule-->
      <div class="rule">
        <div class="rule-item clearfix" v-for="(item,index) in productDetail.rules" :key="index">
          <div class="fl rule-title">{{item.title}}</div>
          <div class="rule-tags clearfix fl">
            <div
              class="fl tag-item"
              v-for="(v,index) in item.tag"
              :key="index"
              :class="{active:v.isActive}"
              @click="toggleRules(item,v)"
            >{{v.name}}</div>
          </div>
        </div>
      </div>

      <!-- 商品描述 -->
      <div class="desc">
        <div class="desc-title">商品描述</div>
        <div class="desc-content">
          <div class="desc-item" v-for="(item,index) in  productDetail.desc" :key="index">{{item}}</div>
        </div>
      </div>
      <!-- 选择数量 步进器 -->
      <div class="clearfix price-box">
        <div class="fl price">￥{{productDetail.price}}</div>
        <div class="fr">
          <van-stepper
            v-model="value"
            :integer="true"
            theme="round"
            button-size="22"
            disable-input
          />
        </div>
      </div>

      <van-goods-action>
        <van-goods-action-icon
          icon="cart-o"
          text="购物车"
          :badge="cartTipNum==0?'': cartTipNum"
          @click="goCart"
        />
        <van-goods-action-button color="#ffd01e" type="warning" text="加入购物车" @click="addCart" />
        <van-goods-action-button
          color="rgb(249,187,50)"
          type="danger"
          text="立即购买"
          @click="addCart(true)"
        />
      </van-goods-action>
    </div>

    <van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" @select="share" />
  </div>
</template>

<script>
import "../assets/css/details.less";
import "../assets/css/icons.css";

export default {
  data() { 
    return {
      value: 1,
      pid: "", //存储商品pid
      productDetail: {}, //存储商品详情数据
      isLike: false, //是否收藏
      count: 1,
      cartTipNum: 0, //购物车 商品条 数 关联 购物车badge
      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" }
      ]
    };
  },

  methods: {
    //返回上一页
    goback() {
      this.$router.go(-1);
    },
    share(option) {
      this.$toast(option.name);
      this.showShare = false;
    },
    //参数pid 获取 商品详情数据
    getProductDetail(pid) {
      //获取商品pid
      
      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 600) {
            //拿出当前商品详情数据productDetail
            let data = result.data.result[0];
            data.desc = data.desc.split(/\n/); //按 换行切换 为数组
            //商品选择规格 根据数据结构 4个类型选择[{title:'温度'，tag:[{name:'冷'},{name:'热'}]}，...]
            let productRule = ["tem", "sugar", "milk", "cream"];

            let rules = []; //数据结构包括 title: v +_desc  tag:{name} / {name}
            productRule.forEach(v => {
              
              if (data[v] != "") {
                // 根据 / 切割为数组 把rule放到rules中  if判断当没有这个规格时就不遍历
                let rule = {
                  title: data[v + "_desc"],
                  tag: []
                };

                let tag = data[v].split(/\//);
                tag.forEach((item, i) => {
                  //处理默认激活状态
                  let t = {
                    name: item,
                    isActive: i == 0 //默认激活0
                  };
                  rule.tag.push(t);
                });

                rules.push(rule);
              }
            });
            
            data.rules = rules; //最后赋值到给data
            this.productDetail = data;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //切换选择 商品规格rules
    toggleRules(item, v) {
      
      

      if (v.isActive) {
        return;
      }
      //查找已激活的规格
      for (let i = 0; i < item.tag.length; i++) {
        if (item.tag[i].isActive) {
          item.tag[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },

    //收藏和取消商品 三个参数pid appkey token(login之前设置为token可以直接获取)
    like() {
      let tokenString = localStorage.getItem("token");

      let url = this.isLike ? "/notlike" : "/like"; //收藏 和 未收藏路径
      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 800 || result.data.code == 900) {
            this.isLike = result.data.code == 800; //900为false
            this.$toast(result.data.msg);
          } else {
            //token无效或者 未登录 就跳转到登录页面
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //查看商品是否被收藏 。 进入详情就可以看到
    findLike(pid) {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 1000) {
            //this.isLike = true;
            this.isLike = result.data.result.length > 0;
          } else {
            //token无效或者 未登录 就跳转到登录页面
            this.$router.push({ name: "Login" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //查询购物车条数。 用户进入后 购物车是否有商品
    findCart() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          // 目的：改变数组长度
          if (result.data.code == 5000) {
            this.cartTipNum = result.data.result.length;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //添加到购物车cart  有5个参数
    addCart(isBuy) {
      let tokenString = localStorage.getItem("token");
      //获取选择商品规格
      let rules = this.productDetail.rules;
      
      let rule = []; //保存标签
      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break; //找到立即打断
          }
        }
      });
      
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          appkey: this.appkey,
          pid: this.pid,
          count: this.count, //绑定存储count
          // rule: this.rule,
          rule: rule.join("/"),
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          //显示左侧this.cartTipNum的购物车数量 新增条数
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.cartTipNum++;
            }
            this.$toast(result.data.msg);
            if (isBuy == true) {
              setTimeout(() => {
                this.$router.push({
                  name: "Pay",
                  query: { sids: result.data.sid }
                });
              }, 500);
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //点击进入购物车
    goCart() {
      this.$router.push({ name: "Cart" });
    }
  },
  created() {
    //截取路由参数pid 给存储的pid 赋值  最后进行传递
    this.pid = this.$route.query.pid;
    this.getProductDetail(this.pid);
    //一进来就需要调用 查询商品是否被收藏
    this.findLike(this.pid);
    this.findCart();
  }
};
</script>

<style lang="less" scoped>
</style>